"use client";

import { useEffect, useRef, useState } from "react";

export default function Page() {
  const localRef = useRef<HTMLVideoElement | null>(null);
  const remoteRef = useRef<HTMLVideoElement | null>(null);

  const [socket, setSocket] = useState<WebSocket | null>(null);
  const [pc, setPc] = useState<RTCPeerConnection | null>(null);

  // store ICE candidates that arrive before remote description is set
  const pendingCandidates = useRef<RTCIceCandidateInit[]>([]);

  useEffect(() => {
    const ws = new WebSocket("ws://localhost:8080");
    setSocket(ws);

    const pc = new RTCPeerConnection({
      iceServers: [{ urls: "stun:stun.l.google.com:19302" }],
    });
    setPc(pc);

    // ðŸ”¹ Local stream
    getStream(pc);

    // ðŸ”¹ Handle remote tracks
    pc.ontrack = (event) => {
      if (remoteRef.current) {
        let stream = remoteRef.current.srcObject as MediaStream;
        if (!stream) {
          stream = new MediaStream();
          remoteRef.current.srcObject = stream;
        }
        stream.addTrack(event.track);
      }
    };

    // ðŸ”¹ Send ICE candidates
    pc.onicecandidate = (event) => {
      if (event.candidate && ws.readyState === WebSocket.OPEN) {
        ws.send(
          JSON.stringify({
            type: "CANDIDATE",
            payload: event.candidate,
          })
        );
      }
    };

    // ðŸ”¹ WebSocket listener
    ws.onmessage = async (msg) => {
      const data = JSON.parse(msg.data);

      if (data.type === "OFFER") {
        const desc = new RTCSessionDescription(data.payload);
        await pc.setRemoteDescription(desc);

        // add queued ICE candidates
        for (const c of pendingCandidates.current) {
          await pc.addIceCandidate(new RTCIceCandidate(c));
        }
        pendingCandidates.current = [];

        const answer = await pc.createAnswer();
        await pc.setLocalDescription(answer);

        ws.send(JSON.stringify({ type: "ANSWER", payload: answer }));
      } else if (data.type === "ANSWER") {
        const desc = new RTCSessionDescription(data.payload);
        await pc.setRemoteDescription(desc);

        // add queued ICE candidates
        for (const c of pendingCandidates.current) {
          await pc.addIceCandidate(new RTCIceCandidate(c));
        }
        pendingCandidates.current = [];
      } else if (data.type === "CANDIDATE") {
        const candidate = data.payload;
        if (pc.remoteDescription) {
          try {
            await pc.addIceCandidate(new RTCIceCandidate(candidate));
          } catch (err) {
            console.error("ICE add error:", err);
          }
        } else {
          // queue candidate until remoteDescription is set
          pendingCandidates.current.push(candidate);
        }
      } else if (data.type === "MATCHED") {
        console.log("Matched, sending offer...");
        sendOffer(pc, ws);
      }
    };

    return () => {
      pc.close();
      ws.close();
    };
  }, []);

  async function getStream(pc: RTCPeerConnection) {
    const stream = await navigator.mediaDevices.getUserMedia({
      video: true,
      audio: true,
    });
    stream.getTracks().forEach((track) => pc.addTrack(track, stream));

    if (localRef.current) {
      localRef.current.srcObject = stream;
      await localRef.current.play();
    }
  }

  async function sendOffer(pc: RTCPeerConnection, ws: WebSocket) {
    const offer = await pc.createOffer();
    await pc.setLocalDescription(offer);
    ws.send(JSON.stringify({ type: "OFFER", payload: offer }));
  }

  return (
    <div className="flex justify-center items-center h-screen w-screen gap-4">
      <div>
        <video
          ref={localRef}
          autoPlay
          muted
          playsInline
          className="w-80 h-60 bg-black"
        />
        <p className="text-center mt-1">Local</p>
      </div>
      <div>
        <video
          ref={remoteRef}
          autoPlay
          playsInline
          className="w-80 h-60 bg-black"
        />
        <p className="text-center mt-1">Remote</p>
      </div>
    </div>
  );
}
